.about {
  height: 100vh;
  display: flex;
  align-items: center;
}
.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}
.a-right {
  flex: 1;
}

.a-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #182867;
}
.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}
.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.a-award-img {
  width: 120px;
  height: 120px;
  border-radius: 20px;
}

.a-title {
  font-weight: 400;
}
.a-sub {
  margin: 20px 0;
}
.a-description {
  font-weight: 300;
  margin: 15px 60px 0 0;
}
.a-award {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.a-award-texts {
  width: 80%;
}

.a-award-title {
  font-weight: bold;
  color: #555;
  margin-bottom: 10px;
}

@media screen and (min-width: 320px) and (max-width: 375px) {
  .a-right {
    margin-top: 50px;
  }
  .a-award-img {
    height: 100px;
    width: 100px;
  }
  .a-award-texts {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .about {
    text-align: center;
    height: 550px;
  }
  .a-left,
  .a-card {
    display: none;
  }
  .a-description {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 15px;
    font-size: 14px;
  }
  .a-award {
    display: flex;
    margin-top: 30px;
    flex-direction: column;
  }
  .a-award-texts {
    margin-top: 10px;
  }
}
