.i {
  display: flex;
  height: 100vh;
}
.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.i-right {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.i-left-wrapper {
  padding: 50px;
}
.i-intro {
  font-size: 60px;
  font-weight: 300;
}
.i-name {
  font-size: 55px;
}
.name {
  font-size: 55px;
  color: #db7093;
}
.i-title {
  height: 50px;
}
.i-title-item {
  font-size: 30px;
  font-weight: bold;
  color: #182867;
}
.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}
.i-img {
  width: 560px;
  height: 535px;
  border-radius: 100%;
  position: absolute;
}
.i-bg {
  clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
  background-color: #db7093;
  width: 100%;
  height: 100%;
  position: absolute;
}

@media screen and (max-width: 390px) {
  .i-name span {
    font-size: 30px;
  }
}
@media screen and (max-width: 768px) {
  .i {
    flex-direction: column;
    height: 600px;
  }
  .i-img {
    width: 200px;
    height: 200px;
  }
  .i-left-wrapper {
    padding: 20px;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .i-description {
    margin: 20px 0 40px 0;
  }
  .i-intro,
  .i-name {
    font-size: 30px;
  }
  .name {
    font-size: 40px;
  }
  .i-title-item {
    font-size: 25px;
  }
  .i-bg,
  .i-scroll {
    display: none;
  }
}
