.c {
  height: 90vh;
  position: relative;
}
.c-bg {
  width: 20px;
  height: 100%;
  background-color: #db7093;
  position: absolute;
}
.c-wrapper {
  margin-top: 125px;
  padding: 0px 50px;
  display: flex;
}
.c-left {
  flex: 1;
}
.c-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.c-title {
  font-size: 60px;
  width: 100%;
}
.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}
.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}
.c-description {
  font-weight: 300;
}
form {
  margin-top: 20px;
}
input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid #ccc;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
  outline: #ccc;
}
textarea {
  width: 90%;
  height: 100px;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
  outline: #ccc;
  resize: none;
}
button {
  border: none;
  padding: 15px;
  background-color: #db7093;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .c {
    height: 100vh;
  }
  .c-wrapper {
    flex-direction: column;
  }
  .c-title {
    font-size: 20px;
  }
  .c-info-item {
    margin: 20px 0px;
    font-size: 14px;
  }
  .c-icon {
    width: 20px;
    height: 20px;
  }
  .c-description {
    display: none;
  }

  form {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  input {
    width: 90%;
    height: 30px;
    margin: 8px;
    margin-left: 0;
  }
  button {
    margin: 8px 0 8px 0;
  }
}
