.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pl-texts {
  width: 65%;
}
.pl-title {
  font-size: 50px;
  font-weight: 600;
}
.pl-desc {
  margin: 20px 0;
}
.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media screen and (max-width: 320px) {
  .pl-desc {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .pl-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    margin-top: 10px;
  }
  .pl-texts {
    width: 100%;
  }
  .pl-list {
    width: 300px;
  }
}
